import React from 'react';
import Analytics from './Analytics';
import Utils, { Logger } from '../utils/Utils';

export default class PluginManager {
  constructor(props) {
    this.props = props;

    this.state = {
      options: null,
      playbackStatus: {}
    };

    this.analytics = new Analytics(props);
  }

  init(player, options) {
    this.uzPlayer = player;
    this.state.options = options;

    this.analytics.init(player, options);
  }

  emit(evt, options) {
    this.analytics.emit(evt, options);
  }

  updatePlaybackStatus(playbackStatus) {
    this.state.playbackStatus = playbackStatus;

    this.analytics.updatePlaybackStatus(playbackStatus);
  }
}
